
















































































































































































































.submit{float: right;}
.action{
  cursor: pointer;
  padding: 0 .75rem !important;
}
#console{
  position: relative;
  height: 120px;
  overflow: auto;
  background: #282c34;
  color: rgba(255,255,255,0.8);
  padding: 10px 15px;
}
#event>p{
  margin: 0 0 2px;
  font-size: 10pt;
  font-family: monospace;
}
#console-name{
  position: absolute;
  top: 0;
  right: 0;
  font-size: 11px;
  padding: 2px 4px;
  background: rgba(255, 255, 255, 0.1);
}
.tabs-wrap{
  box-sizing: border-box;
  padding: 0.5rem 1rem;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.2);
  min-height: 20rem;
}
